/* Base CSS */

html,
body {
    padding: 0;
    margin: 0;
}

.animate-5 {
    transition: all 0.5s ease-in-out;
}


/* Scrollbar */

::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #F5F5F5;
}

::-webkit-scrollbar {
    width: 12px;
    background-color: #F5F5F5;
}

::-webkit-scrollbar-thumb {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    background-color: #541818;
}

::-webkit-scrollbar-thumb:hover {
    background-color: #6e2020;
}


/* Grid Container */

.App {
    display: grid;
    grid-template-areas: 'sidebar content';
    grid-template-columns: calc(100vh / 6) auto;
    grid-gap: 0;
}


/* Background */

.background-image {
    position: absolute;
    display: flex;
    width: calc(100% + (100vh/6));
    height: 100%;
    min-height: 400px;
    overflow: hidden;
    left: calc(( -100vh/ 6));
    z-index: -1;
}

.background-image img {
    width: auto;
}


/* Sidebar */

.sidebar {
    grid-area: sidebar;
    display: grid;
    height: 100vh;
    overflow-y: hidden;
}

.sidebarSticky {
    height: 100vh;
    position: fixed;
    top: 0;
    z-index: 2;
}

.sidebar .sidebarElement {
    display: flex;
    height: calc(100vh / 5);
    width: calc(100vh / 6);
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow-x: hidden;
    transition: opacity 0.5s ease-in-out;
}

.sidebar .sidebarElement a {
    font-family: 'Building', sans-serif;
    font-size: 1.5em;
    text-decoration: none;
    text-transform: uppercase;
    color: #ffffff;
    opacity: 0.15;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sidebar .sidebarElement.active {
    background-color: #54181894;
    background-blend-mode: overlay;
}

.sidebar .sidebarElement.active a {
    color: #ff4c4c;
    opacity: 1;
}

.sidebar .sidebarElement.active .sidebarBreak {
    -webkit-filter: invert(75%) sepia(88%) saturate(1632%) hue-rotate(337deg) brightness(100%) contrast(89%);
            filter: invert(75%) sepia(88%) saturate(1632%) hue-rotate(337deg) brightness(100%) contrast(89%);
    opacity: 1;
    box-shadow: 0 0 11px 0 hsla(0, 0%, 100%, 0.80);
}

.sidebarBreak {
    width: 100%;
    position: absolute;
    bottom: 0;
    opacity: 0.3;
    transition: opacity 0.5s ease-in-out, box-shadow 0.1s ease-out;
}

.sidebarElement:hover {
    cursor: pointer;
}

.sidebarElement:hover a {
    opacity: 0.3;
}

.sidebarElement:hover .sidebarBreak {
    opacity: 1;
}


/* Pages */

.page {
    width: 100%;
    height: 100%;
    position: relative;
}


